
#app {
}
body{
  margin:0 auto;
}

#siderNav{
	width:200px;
	height:700px;
	float: left;
	background-color: #e6e6e6;
}
img{
	width: 20px;
	margin-right: 10px;
}
ul{
	padding-left: 30px;
}
ul li{
	font-size: 14px;
	list-style: none;
	margin-bottom: 12px;
	cursor: pointer;
}
a{
	text-decoration: none;
	color: black;
}
.router-link-active{
	color: #da4436
}

#header{
    height: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    background-color: #da4436;
}
.header-search{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-left: 400px;
}
.search-form{
    width: 250px;
    border:1px solid hsla(0,0%,59.2%,.2);
    background-color: rgba(227,231,236,.1);
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border-radius: 12px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    height: 30px;
}
.search-form .search-input{
    width: 200px;
    padding-left: 10px;
    background-color: transparent;
    border-style: none;
    color: white;
}
.search-input::-webkit-input-placeholder{
    color:rgb(243, 220, 220);
}
.header-login{
    margin-left: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.header-set{
    margin-left: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.find{
  float: left;
}




















































